/*
* tools
*/
header {
  position: fixed;
  width: 100%;
  z-index: 99;
  padding: .4rem 1rem; }
  header nav {
    float: right; }
    header nav a {
      font-size: .28rem;
      font-weight: 400;
      margin: 0 0.5rem 0 0;
      position: relative;
      display: inline-block; }
      header nav a:after {
        content: " ";
        transition: all .5s;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: transparent; }
      header nav a.active:after, header nav a:hover:after {
        transition: all .5s;
        background-color: #3ff8ff;
        bottom: -.15rem; }
      header nav a:last-of-type {
        margin-right: 0; }
    header nav #aboutus {
      padding: 0.08rem .25rem;
      border-radius: .8rem;
      border: 1px solid #f1f3f5; }
      header nav #aboutus:after {
        display: none; }
  header #personinfo {
    display: none;
    position: absolute;
    width: 140px;
    right: 0;
    top: 0;
    height: 100%; }
    header #personinfo #avatar {
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      margin-top: 0.3rem;
      margin-left: 0.2rem; }
    header #personinfo #username {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-size: .28rem;
      margin-left: .2rem; }
    header #personinfo ul {
      position: absolute;
      width: 100%;
      padding-left: 0.2rem;
      background: rgba(0, 0, 0, 0.1);
      top: 100%;
      left: 0;
      overflow: hidden;
      height: 0;
      transition: all .5s; }
      header #personinfo ul li {
        font-size: .3rem;
        line-height: 1.2rem; }
    header #personinfo:hover ul {
      height: 1.2rem; }
  header.haslogined {
    padding-right: 140px; }
    header.haslogined #login,
    header.haslogined #reg {
      display: none; }
    header.haslogined #personinfo {
      display: block; }

